* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

img, video, canvas {
  max-width: 100%; }

@keyframes fog {
  0% {
    transform: translateX(-10px); }
  50% {
    transform: translateX(10px); }
  100% {
    transform: translateX(-10px); } }

@keyframes moving {
  0% {
    transform: translateX(-10px); }
  50% {
    transform: translateX(10px); }
  100% {
    transform: translateX(-10px); } }

@keyframes raining {
  0%,
  60%,
  100% {
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }
  0% {
    opacity: 0; }
  60% {
    opacity: 1; }
  100% {
    opacity: 1; } }

@keyframes snowing {
  0% {
    opacity: 0; }
  20% {
    opacity: 1;
    transform: translateY(6px); }
  40% {
    opacity: 1;
    transform: translateY(12px); }
  60% {
    opacity: 1;
    transform: translateY(18px); }
  80% {
    opacity: 1;
    transform: translateY(24px); }
  100% {
    opacity: 0;
    transform: translateY(30px); } }

body {
  font-family: sans-serif;
  /* overflow-x:hidden;  */ }

.mainPart {
  position: relative;
  top: 15vh;
  text-align: center; }

.circleDiv {
  width: 5em;
  height: 5em;
  position: absolute;
  top: -5vh;
  right: 4vh; }

@media (max-width: 400px) {
  .circleDiv {
    position: absolute;
    right: 1vh;
    top: -5vh; } }

@media (min-width: 1050px) {
  .circleDiv {
    top: -3vh; } }

.buttonRound {
  border-radius: 50%;
  height: 100%;
  width: 100%;
  background-color: lightgrey; }

.searchBar {
  display: inline-block; }

i.fa-bars {
  color: #8d90a1; }

.coldColor {
  background-color: rgba(63, 191, 191, 0.48);
  text-align: center; }

.warmColor {
  background-color: rgba(247, 196, 43, 0.65);
  text-align: center; }

/* Local Storage */
.localStorageDisplay {
  background-color: white;
  position: relative;
  min-height: 20vh; }

.storedLocation {
  padding-top: 3vh;
  padding-bottom: 3vh;
  display: flex;
  flex-direction: row;
  justify-content: center; }

.storedName:hover {
  cursor: pointer; }

/* Weather */
.weather {
  min-height: 24vh; }

.weatherName {
  font-size: 3em; }

.weatherTemp {
  font-size: 3.5em; }

@media (max-width: 450px) {
  .weatherName {
    font-size: 2em; }
  .weatherTemp {
    font-size: 2em; } }

/* Quote */
.quoteTitle {
  font-size: 1.3em;
  color: #8d90a1; }

.quoteContent {
  font-size: 2em;
  font-style: italic; }

@media (max-width: 450px) {
  .quoteContent {
    font-size: 1em; }
  .quoteTitle {
    font-size: 0.7em; } }

